//Question 5

const CalcBMI = () => {
  const [userWeight, setWeight] = useState();
  const [userHeight, setHeight] = useState();
  const [bmi, setBMI] = useState();

  const heightChangehandler = (event) => {
    console.log(event.target.value);
    setHeight(event.target.value);
  };
  const weightChangehandler = (event) => {
    console.log(event.target.value);
    setWeight(event.target.value);
  };
  const calculateBMI = () => {
    let bmi = (userWeight / (userHeight / 100) ** 2).toFixed(2);
    setBMI(bmi);
  };

  return (
    <div className="bmi">
      <InputText
        heading="Enter Height : "
        value={userHeight}
        placeholder="Enter your height in cm"
        handleOnChange={heightChangehandler}
      />
      <br></br>
      <InputText
        heading="Enter Weight : "
        value={userWeight}
        placeholder="Enter your weight in kg"
        handleOnChange={weightChangehandler}
      />
      <br></br>
      <br></br>
      <Button onClick={calculateBMI} description="Generate BMI" />
      <br></br>
      <br></br>
      <label>Your BMI : {bmi}</label>
    </div>
  );
};

export default CalcBMI;
